import * as actionsTypes from '../actions/actionsTypes';
import {updateObject} from '../utility';

const initialState ={
     ingredients: null,
     totalPrice: 4,
     error: false
}

const INGREDIENT_PRICES = {
     salad: 0.5,
     cheese: 0.4,
     meat: 1.3,
     bacon: 0.7
};

const addIngredient = (state, action) =>{
     const updatedIngredient = { [action.ingredientName]: state.ingredients[action.ingredientName] + 1 }
     const updatedIngredients = updateObject( state.ingredients, updatedIngredient );
     const updatedState = {
          ingredients: updatedIngredients,
          totalPrice: state.totalPrice + INGREDIENT_PRICES[action.ingredientName]
     }
     return updateObject( state, updatedState );
}

const removeIngredient = (state, action) =>{
     const updatedIng = { [action.ingredientName]: state.ingredients[action.ingredientName] - 1 }
     const updatedIngs = updateObject( state.ingredients, updatedIng );
     const updatedSt = {
          ingredients: updatedIngs,
          totalPrice: state.totalPrice + INGREDIENT_PRICES[action.ingredientName]
     }
     return updateObject( state, updatedSt );
}

const setIngredient = (state, action ) => {
     return updateObject(state,{
          ingredients: {
               salad: action.ingredients.salad,
               bacon: action.ingredients.bacon,
               cheese: action.ingredients.cheese,
               meat: action.ingredients.meat
          },
          totalPrice: 4,
          error: false
     });
}

const fetchIngredientsFailed = (state, action) =>{
     return updateObject(state,{error: true});
}

const reducer = (state = initialState,action)=>{
     switch(action.type){
          case actionsTypes.ADD_INGREDIENT: return addIngredient(state,action);   
          case actionsTypes.REMOVE_INGREDIENT: return removeIngredient(state,action);
          case actionsTypes.SET_INGREDIENTS: return setIngredient(state,action);
          case actionsTypes.FETCH_INGREDIENTS_FAILED: return fetchIngredientsFailed(state,action); 
          default: return state;
     }
};

export default reducer;


